import React from 'react';
import { CollectionPage } from '@wix/patterns/page';
import { AutoPatternsCollectionPageContent } from '../AutoPatternsCollectionPageContent';
import { usePatternsWizardOverridesContext } from '../../providers';
import { CollectionPageConfig } from '../../types/CollectionPageConfig';
import { useCollectionPageActions } from '../../hooks/useCollectionPageActions';

export interface AutoPatternsCollectionPageProps {
  configuration: CollectionPageConfig;
}

export const AutoPatternsCollectionPage: React.FC<
  AutoPatternsCollectionPageProps
> = ({ configuration }) => {
  const { title, subtitle } = configuration;

  const actions = useCollectionPageActions({
    collectionPageActions: configuration.actions,
  });

  const context = usePatternsWizardOverridesContext();

  return (
    <CollectionPage height="100vh" dataHook="auto-patterns-page">
      <CollectionPage.Header
        title={title}
        subtitle={subtitle}
        primaryAction={actions.primaryActions}
        secondaryActions={actions.secondaryActions}
      />
      <CollectionPage.Content>
        {context?.HeaderAddon ? <context.HeaderAddon /> : null}
        <AutoPatternsCollectionPageContent configuration={configuration} />
      </CollectionPage.Content>
    </CollectionPage>
  );
};
